"use client"

import type React from "react"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group"
import { ArrowLeft, CreditCard, Wallet, Smartphone } from "lucide-react"

interface PaymentProcessProps {
  rideData: {
    pickup: string
    destination: string
    vehicle: string
    price: number
  }
  onComplete: () => void
  onBack: () => void
}

export function PaymentProcess({ rideData, onComplete, onBack }: PaymentProcessProps) {
  const [paymentMethod, setPaymentMethod] = useState("card")
  const [cardNumber, setCardNumber] = useState("")
  const [cardExpiry, setCardExpiry] = useState("")
  const [cardCvc, setCardCvc] = useState("")
  const [processing, setProcessing] = useState(false)

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    setProcessing(true)

    // Simulate payment processing
    setTimeout(() => {
      setProcessing(false)
      onComplete()
    }, 2000)
  }

  return (
    <div className="min-h-screen bg-background">
      {/* Header */}
      <header className="sticky top-0 z-10 border-b border-border bg-card px-6 py-4">
        <div className="mx-auto flex max-w-4xl items-center gap-4">
          <Button variant="ghost" size="icon" onClick={onBack} disabled={processing}>
            <ArrowLeft className="h-5 w-5" />
          </Button>
          <h1 className="text-xl font-bold">Confirm and pay</h1>
        </div>
      </header>

      <div className="mx-auto max-w-4xl px-6 py-8">
        <div className="grid gap-8 lg:grid-cols-5">
          {/* Payment Form */}
          <div className="lg:col-span-3">
            <Card className="p-6">
              <h2 className="mb-6 text-xl font-bold">Payment method</h2>

              <RadioGroup value={paymentMethod} onValueChange={setPaymentMethod} className="mb-6">
                <div className="flex items-center space-x-3 rounded-lg border border-border p-4">
                  <RadioGroupItem value="card" id="card" />
                  <Label htmlFor="card" className="flex flex-1 cursor-pointer items-center gap-3">
                    <CreditCard className="h-5 w-5" />
                    <span>Credit/debit card</span>
                  </Label>
                </div>

                <div className="flex items-center space-x-3 rounded-lg border border-border p-4">
                  <RadioGroupItem value="wallet" id="wallet" />
                  <Label htmlFor="wallet" className="flex flex-1 cursor-pointer items-center gap-3">
                    <Wallet className="h-5 w-5" />
                    <span>Digital wallet</span>
                  </Label>
                </div>

                <div className="flex items-center space-x-3 rounded-lg border border-border p-4">
                  <RadioGroupItem value="cash" id="cash" />
                  <Label htmlFor="cash" className="flex flex-1 cursor-pointer items-center gap-3">
                    <Smartphone className="h-5 w-5" />
                    <span>Cash</span>
                  </Label>
                </div>
              </RadioGroup>

              {paymentMethod === "card" && (
                <form onSubmit={handleSubmit} className="space-y-4">
                  <div>
                    <Label htmlFor="cardNumber">Card number</Label>
                    <Input
                      id="cardNumber"
                      type="text"
                      placeholder="1234 5678 9012 3456"
                      value={cardNumber}
                      onChange={(e) => setCardNumber(e.target.value)}
                      maxLength={19}
                      required
                      className="mt-1"
                    />
                  </div>

                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <Label htmlFor="expiry">Expiration date</Label>
                      <Input
                        id="expiry"
                        type="text"
                        placeholder="MM/YY"
                        value={cardExpiry}
                        onChange={(e) => setCardExpiry(e.target.value)}
                        maxLength={5}
                        required
                        className="mt-1"
                      />
                    </div>

                    <div>
                      <Label htmlFor="cvc">CVC</Label>
                      <Input
                        id="cvc"
                        type="text"
                        placeholder="123"
                        value={cardCvc}
                        onChange={(e) => setCardCvc(e.target.value)}
                        maxLength={4}
                        required
                        className="mt-1"
                      />
                    </div>
                  </div>

                  <Button type="submit" className="w-full" size="lg" disabled={processing}>
                    {processing ? "Processing..." : "Confirm and request"}
                  </Button>
                </form>
              )}

              {paymentMethod !== "card" && (
                <Button onClick={onComplete} className="w-full" size="lg" disabled={processing}>
                  {processing ? "Processing..." : "Confirm and request"}
                </Button>
              )}
            </Card>
          </div>

          {/* Ride Summary */}
          <div className="lg:col-span-2">
            <Card className="sticky top-24 p-6">
              <h2 className="mb-4 text-xl font-bold">Ride summary</h2>

              <div className="space-y-4">
                <div>
                  <p className="text-sm text-muted-foreground">Pickup</p>
                  <p className="font-medium">{rideData.pickup}</p>
                </div>

                <div>
                  <p className="text-sm text-muted-foreground">Destination</p>
                  <p className="font-medium">{rideData.destination}</p>
                </div>

                <div className="border-t border-border pt-4">
                  <p className="text-sm text-muted-foreground">Vehicle</p>
                  <p className="font-medium">{rideData.vehicle}</p>
                </div>

                <div className="border-t border-border pt-4">
                  <div className="flex items-center justify-between">
                    <span className="text-sm">Base fare</span>
                    <span className="font-medium">${rideData.price.toFixed(2)}</span>
                  </div>
                  <div className="flex items-center justify-between">
                    <span className="text-sm">Service fees</span>
                    <span className="font-medium">$2.50</span>
                  </div>
                </div>

                <div className="border-t border-border pt-4">
                  <div className="flex items-center justify-between">
                    <span className="text-lg font-bold">Total</span>
                    <span className="text-2xl font-bold">${(rideData.price + 2.5).toFixed(2)}</span>
                  </div>
                </div>
              </div>
            </Card>
          </div>
        </div>
      </div>
    </div>
  )
}
